





















































import Vue from "vue";
import Cookies from "js-cookie";

export default Vue.extend({
  methods: {
    welcomeMessage(): string {
      const name = Cookies.get("first_name");
      if (name) {
        return `Hello ${name}, welcome to your dashboard!`;
      } else {
        return `Hello, welcome to your dashboard!`;
      }
    },
  },
});
